import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import { store } from './store'
import ACCESS_ENUM from "@/access/ACCESSNUM";
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'

const app = createApp(App);

Object.keys(ElementPlusIconsVue).forEach((key) => {
    app.component(key, (ElementPlusIconsVue as any)[key]); // 使用类型断言
});

// 设置路由守卫
router.beforeEach((to, from, next) => {
    const requiredAccess = to.meta?.access || ACCESS_ENUM.NOT_LOGIN;
    const currentUserRole = store.state.user?.userRole;

    if (Array.isArray(requiredAccess)) {
        if (!requiredAccess.includes(currentUserRole)) {
            // 如果用户的角色不在所需的权限数组中，重定向到登录页面
            next("/login");
            store.dispatch('logout')
            return;
        }
    } else {
        if (requiredAccess === ACCESS_ENUM.ADMIN && currentUserRole !== ACCESS_ENUM.ADMIN) {
            next("/login");
            store.dispatch('logout')
            return;
        }

        if (requiredAccess === ACCESS_ENUM.USER && currentUserRole !== ACCESS_ENUM.USER) {
            next("/login");
            store.dispatch('logout')
            return;
        }
    }
    next();

});



app.use(store).use(router).use(ElementPlus).mount('#app');
