import { createStore } from 'vuex';
import Cookies from 'js-cookie';
import ACCESS_ENUM from "@/access/ACCESSNUM";
import createPersistedState from "vuex-persistedstate"
interface State {
  user: {
    token: string | null,
    permissions: string[],
    userRole: string,
    username: string | null
  } | null;
  userinfo: {
    ranking: string | null,
    name: string | null,
    web: string | null,
    misc: string | null,
    crypto: string | null,
    reverse: string | null,
    pwn: string | null,
    totalscore: string | null,
    finalSubmission: string | null,
    address: string | null
  } | null
}
export const store = createStore<State>({
  state: {
    user: {
      token: null,
      permissions: [ACCESS_ENUM.NOT_LOGIN],
      userRole: ACCESS_ENUM.NOT_LOGIN,
      username: null
    },
    userinfo: {
      ranking: null,
      name: null,
      web: null,
      misc: null,
      crypto: null,
      reverse: null,
      pwn: null,
      totalscore: null,
      finalSubmission: null,
      address: ACCESS_ENUM.NOT_LOGIN,
    }
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user;
      if (user && user.token) {
        Cookies.set('Run_Token', user.token);
      } else {
        Cookies.remove('Run_Token');
      }
    }, SET_USERINFO(state, userinfo) {
      state.userinfo = userinfo
    }
  },
  actions: {
    login({ commit }, user) {
      commit('SET_USER', user);
    },
    logout({ commit }) {
      commit('SET_USER', {
        token: null,
        permissions: [ACCESS_ENUM.NOT_LOGIN],
        userRole: ACCESS_ENUM.NOT_LOGIN,
        username: null
      });
    },
    showuserinfo({ commit }, userinfo) {
      commit('SET_USERINFO', userinfo)
    },
    outshowuserinfo({ commit }) {
      commit('SET_USERINFO', {
        ranking: null,
        name: null,
        web: null,
        misc: null,
        crypto: null,
        reverse: null,
        pwn: null,
        totalscore: null,
        finalSubmission: null,
        address: ACCESS_ENUM.NOT_LOGIN,
      });
    },
  },
  getters: {
    isLogged: (state: State) => !!state.user?.token,
    hasPermission: (state: State) => (permission: string) => {
      return state.user?.permissions.includes(permission) || false;
    }
  },
  plugins: [
    createPersistedState({
      storage: window.localStorage,
      key: "RunCtf",
      reducer(state) {
        return { user: state.user, userinfo: state.userinfo }
      },
    })
  ]
});
