import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import ACCESS_ENUM from "@/access/ACCESSNUM";

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      access: [ACCESS_ENUM.ADMIN, ACCESS_ENUM.USER]
    },
    children: [
      {
        path: '',
        alias: 'index',
        name: 'index',
        component: () =>
          import(/* webpackChunkName: "IndexView" */ '../views/Home/IndexView.vue'),
      },
      {
        path: 'thecharts',
        alias: 'thecharts',
        name: 'thecharts',
        component: () =>
          import(/* webpackChunkName: "TheChartsView" */ '../views/Home/TheChartsView.vue'),
      },
      {
        path: 'challenge',
        alias: 'challenge',
        name: 'challenge',
        component: () =>
          import(/* webpackChunkName: "ChallenGeView" */ '../views/Home/ChallenGeView.vue'),
      },
    ]
  },
  {
    path: '/login',
    name: 'login',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../components/HelloWorld.vue'),
    meta: {
      access: ACCESS_ENUM.NOT_LOGIN
    }
  },
  {
    path: '/userinfo',
    name: 'userinfo',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/UserInfo/IndexView.vue'),
    meta: {
      access: [ACCESS_ENUM.ADMIN, ACCESS_ENUM.USER]
    },
  },
  {
    path: '/tableuserinfo',
    name: 'tableuserinfo',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Home/TableUserInfo/TableUserInfoView.vue'),
    meta: {
      access: [ACCESS_ENUM.ADMIN, ACCESS_ENUM.USER]
    },
  },
  {
    path: '/runadmin',
    name: 'runadmin',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Run_Admin/AdminView.vue'),
    meta: {
      access: [ACCESS_ENUM.ADMIN]
    },
    children: [
      {
        path: '',
        alias: 'adminindex',
        name: 'adminindex',
        component: () =>
          import(/* webpackChunkName: "IndexView" */ '@/views/Run_Admin/ControlPanel/IndexView.vue'),
      },
      {
        path: 'questionmanagement',
        alias: 'questionmanagement',
        name: 'questionmanagement',
        component: () =>
          import(/* webpackChunkName: "TheChartsView" */ '@/views/Run_Admin/CompetitionManagement/QuestionManagementView.vue'),
      },
    ]
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
